<template>
  <div class="modal warning fade" :class="{'is-open': open}">
    <div class="modal-dialog modal-sm">
      <div class="modal-content">
        <div class="modal-body">
          <h4 class="mt-0 mb-3">CRM</h4>
          <div class="row item update-info"
          :class="{'is-update': !customer.name.newValue==false}">
            <div class="col-3 text-right"><b></b></div>
            <div class="col-9 item-content">
              <div class="content">
                <span class="text">{{customer.name.value}}</span>
                <span class="text text-red"
                v-if="!customer.name.newValue==false">
                  <span class="arrow">
                    
                  </span>
                  <span class="text">{{ customer.name.newValue }}</span>
                </span>
              </div>
              <div class="action">
                <span class="label red"
                v-if="!customer.name.newValue==false">
                  
                </span>
              </div>
            </div>
          </div>
          <div class="row item update-info"
          :class="{'is-update': !customer.identityID.newValue==false}">
            <div class="col-3 text-right"><b></b></div>
            <div class="col-9">
              {{customer.identityID.value}}
              <span class="update-info text-red"
              v-if="!customer.identityID.newValue==false">
                <span class="arrow">
                  
                </span>
                <span class="text">{{ customer.identityID.newValue }}</span>
              </span>  
              <div class="action">
                <span class="label red"
                v-if="!customer.identityID.newValue==false">
                  
                </span>
              </div>   
            </div>
          </div>
          <div class="row item update-info"
          :class="{'is-update': !customer.birthday.newValue==false}">
            <div class="col-3 text-right"><b></b></div>
            <div class="col-9">
              {{customer.birthday.value | dateFormat }}
              <span class="update-info text-red"
              v-if="!customer.birthday.newValue==false">
                <span class="arrow">
                  
                </span>
                <span class="text">{{ customer.birthday.newValue }}</span>
              </span>
            </div>
          </div>
          <div class="row item update-info"
          :class="{'is-update': !customer.mobile.newValue==false}">
            <div class="col-3 text-right"><b></b></div>
            <div class="col-9">
              {{customer.mobile.value | emptyField }}
              <span class="update-info text-red"
              v-if="!customer.mobile.newValue==false">
                <span class="arrow">
                  
                </span>
                <span class="text">{{ customer.mobile.newValue }}</span>
              </span>
            </div>
          </div>
          <div class="row item update-info"
          :class="{'is-update': !customer.phone.newValue==false}">
            <div class="col-3 text-right"><b></b></div>
            <div class="col-9">
              {{customer.phone.value | emptyField}}
              <span class="update-info text-red"
              v-if="!customer.phone.newValue==false">
                <span class="arrow">
                  
                </span>
                <span class="text">{{ customer.phone.newValue }}</span>
              </span>
            </div>
          </div>
          <div class="row item update-info"
          :class="{'is-update': !customer.address.newValue==false}">
            <div class="col-3 text-right"><b></b></div>
            <div class="col-9">
              {{customer.address.value | emptyField }}
              <span class="update-info text-red"
              v-if="!customer.address.newValue==false">
                <span class="arrow">
                  
                </span>
                <span class="text">{{ customer.address.newValue }}</span>
              </span>
            </div>
          </div>

          <div class="action text-right">
            <a ref="btn_cancel" v-show="showCancel"
            @click="doCancel" class="btn btn-default"></a>
            <a ref="btn_submit"
            @click="doSubmit" class="btn btn-primary"></a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { selectMsgCustInfo } from '../../service'
import { updCustMsgCust } from '../../service'

export default {
  name: 'UpdateMsgModal',
  props: {
    open: {
      type: Boolean,
      default: false
    },
    id: { // user id get detail
      type: Number,
      default: null 
    },
    custMsgInfo: {
      type: Object, 
      default: {}
    }
  },
  watch: {
    'id': 'getCustInfo'
  },
  methods: {
    getCustInfo(val) {
      if(this.custMsgInfo.id) {
        selectMsgCustInfo(this.$store.state.userInfo.id, this.custMsgInfo.id).then(result => {
          if(result.header.status === '200' ) {
            this.customer.identityID.value = this.custMsgInfo.id
            this.customer.name.value = this.custMsgInfo.bef_name
            if(this.custMsgInfo.sub_trn_kind === 'ABQCID') {
              this.customer.identityID.newValue = this.custMsgInfo.aft_data
              this.customer.name.newValue = ''

              this.showCancel = false
              this.$refs.btn_submit.innerHTML = ''
            }
            if(this.custMsgInfo.sub_trn_kind === 'ABQCNM') {
              this.customer.name.newValue = this.custMsgInfo.aft_name
              this.customer.identityID.newValue = ''

              this.showCancel = true
              this.$refs.btn_cancel.innerHTML = ''
              this.$refs.btn_submit.innerHTML = ''
            }
            let queryInfo = result.body || {}

            this.customer.birthday.value = queryInfo.birthday
            this.customer.mobile.value = queryInfo.mobile
            if(queryInfo.home_tel_area || queryInfo.home_tel) {
              this.customer.phone.value = queryInfo.home_tel_area + '-' + queryInfo.home_tel
            } else {
              this.customer.phone.value = ''
            }
            
            this.customer.address.value = queryInfo.home_addr1 + queryInfo.home_addr2 + queryInfo.home_addr3
            this.custMsgInfo.check_customers = queryInfo.check_customers
            this.custMsgInfo.agid = this.$store.state.userInfo.id
          } else {
            this.$toast.info({title: result.header.message })   
          }
       })
      }
    },
    doSubmit() {
      this.custMsgInfo.confirm_type = '1'
      updCustMsgCust(this.$store.state.userInfo.id, this.custMsgInfo).then(result => {
        if(!result.header.states === '200') {
          this.$toast.info({title: result.header.message })   
        }
        this.$emit('submit')
      })
    },
    doCancel() {
      this.custMsgInfo.confirm_type = '0'
      updCustMsgCust(this.$store.state.userInfo.id, this.custMsgInfo).then(result => {
        if(!result.header.states === '200') {
          this.$toast.info({title: result.header.message })   
        }
        this.$emit('submit')
      })
    }
  },
  data () {
    return {
      customer: {
        name: {
          value: ''
        },
        identityID: {
          value: ''
        },
        birthday: {
          value: ''
        },
        mobile: {
          value: ''
        },
        phone: {
          value: ''
        },
        address: {
          value: ''
        }
      },
      msgInfo: {}, 
      showCancel: true
    }
  }
}

</script>
