import Vue from 'vue'
import { log } from 'util';
// Set filter here
Vue.filter("numberFormat", function(value) {
  if(!value && value!==0) {
    return '--'
  } else if(typeof value == 'string') {
    return value
  } else {
    let val = (value / 1).toFixed(0).replace(".", ",");
    return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }
})

Vue.filter("addressFormat", function(value) {
  if(!value) {
    return '--'
  } else if(typeof value == 'string') {
    return value
  } else {
    return value.toString().replace(/,/g, '')
  }
})

Vue.filter("phoneFormat", function(value) {
  if(!value) {
    return '--'
  } else if(typeof value == 'string') {
    return value
  } else {
    let starRegex = /(\*)/
    let mergeStr = value.toString().replace(/,/g, '-')
    let phonePattern = new RegExp('[0-9]+(-[0-9]+)?(-[0-9]+)?')
    let resultStr = phonePattern.exec(mergeStr)
    let result = mergeStr[mergeStr.search(starRegex)]
    if(result == "*"){
      return mergeStr
    }else{
      return resultStr ? resultStr[0] : ''
    }
  }
})

Vue.filter("dateFormat", function(value) {
  let validateEmpty = /\d{4}\-/g
  if (!value || value==='' || !validateEmpty.test(value)) return ' '
  let dt = window.moment(value)
  let m = dt.month() + 1 < 10 ? `0${dt.month() + 1}` : dt.month() + 1
  let d = dt.date() < 10 ? `0${dt.date()}` : dt.date()
  return `${dt.year() - 1911}/${m}/${d}`
})

Vue.filter("emptyField", function(value) {
  return ((value!==0 && !value) || value === '') ? ' ' : value
})
Vue.filter("emptyDashField", function(value) {
  return ((value!==0 && !value) || value === '') ? '-' : value
})

Vue.filter("ageCounter", function(value) {
  if(value === '' || value === '--'){
    return ' '
  }else{
    let dt = window.moment(value)
    let today = window.moment()
    return today.year() - dt.year()
  }
  
});

Vue.filter("slashDateFormat", function(value) {
  
  if (!value || value==='') {return ' '
  }else{
    let newVal = value.split('')
    let newD = newVal.splice(newVal.length - 2,2)
    let newM = newVal.splice(newVal.length - 2,2)
    newVal = newVal.join('')
    newM = newM.join('')
    newD = newD.join('')
    return `${newVal}/${newM}/${newD}`
  }
})
Vue.filter("getPhoneNum", function(value) {
  if(!value || value==='') return ' '
  let num = value.split('#')
  return num[0]
})
Vue.filter("getPhoneExt", function(value) {
  
  if(!value || value==='') return ' '
  let ext = value.substring(value.lastIndexOf("#")+1)
  return ext
})

Vue.filter("dateFormatRemoveHMS", function(value){
  if (!value || value==='') return ' '
  let dt = window.moment(value)
  let m = dt.month() + 1 < 10 ? `0${dt.month() + 1}` : dt.month() + 1
  let d = dt.date()
  return `${dt.year()}/${m}/${d}`
})
