/*
* addNewGroup @parameter agnt_id,grp_name,members
* updateOldGroup @parameter agnt_id,grp_id,grp_name,members
*/
export const updateGroup = function(agnt_id, grp_id, grp_name, members, api) {
  let memFormatedArr = []
  members.forEach((mem) => {
    let eachRecord = {
      agnt_id: agnt_id,
      id: mem.identity,
      grpid: grp_id,
      grp_name: grp_name
    }
    memFormatedArr.push(eachRecord)
  })
  const body = {
    header: {
      userDeviceIp : clientIP,
      userId: agnt_id
    },
    body : {
      api_id: api,
      report: {
        agnt_id: agnt_id,
        grpid: grp_id,
        grp_name: grp_name,
        recordsList: memFormatedArr
      }
    }
  }
  return body
}

