/*
* &
* 
* headerData (   )
* bodyData (   email )
*
* ,data,null
*/
export const editCustInfo = function(userId, csid, headerData, bodyData) {
  let transHeaderData = {}
  let transBodyData = {}

  if(headerData) {
    transHeaderData = {
      name: headerData.name,
      ser_no: headerData.serNo,
      id: headerData.ID,
      birthday: headerData.birthday,
      sex: headerData.gender,
      mobile: headerData.mobile,
      home_tel_area: headerData.phone[0],
      home_tel: headerData.phone[1],
      corp_tel_area: headerData.office_phone[0],
      corp_tel: headerData.office_phone[1],
      corp_tel_ext: headerData.office_phone[2],
      home_address: {
        home_addr_zip: headerData.address[0],
        home_addr1: headerData.address[1],
        home_addr2: headerData.address[2],
        home_addr3: headerData.address[3],
      },
      corp_address: {
        corp_addr_zip: headerData.office_address[0],
        corp_addr1: headerData.office_address[1],
        corp_addr2: headerData.office_address[2],
        corp_addr3: headerData.office_address[3],
      },
      lineid: headerData.lineid,
      wg_name: headerData.wg_name
    }
  }
  if(bodyData) {
    transBodyData = {
      email: bodyData.email,
      introducer: bodyData.introducerId,
      job: bodyData.career,
      corp: bodyData.agency,
      edu: bodyData.education,
      marry: bodyData.marriage,
      fax_area: bodyData.fax[0],
      fax: bodyData.fax[1],
      ser_no: bodyData.serNo,
    }
  }

  let body = {
    header: {
      userDeviceIp: clientIP,
      userId: userId
    },
    body: {
      report: {
        agnt_id: userId,
        csid: csid,
        info: transHeaderData,
        data: transBodyData
      }
    }
  }
  return body
}

