<template>
    <section class="insurance-table newTable" v-if="options">
      <table class="table datatable table-striped table-bordered" :id="prefix">
        <thead >
          <tr>
            <th v-if="options.checkAll" width="60">
              <div class="checkbox checkbox-inline mr-0" v-if="multiple">
                <input type="checkbox" :name="`${prefix}-check-all`" :id="`${prefix}-check-all`" value="1" v-model="checkAll">
                <label :for="`${prefix}-check-all`"></label>
              </div>
            </th>
            <th
            v-for="(item, index) in options.columns"
            :key="`${prefix}-table-tr-${index}`"
            :width="(index+1===options.columns.length ? item.width + 17 : item.width) || '*'"
            :class="{
              'sorting': item.sorting,
              'sorting_asc': order.index==index + 1 && order.asc==true,
              'sorting_desc': order.index==index + 1 && !order.asc,
              'text-right': item.align && item.align=='right'
            }"
            @click="columnSorting(index + 1)"
            >
                <span v-html="item.label"></span>
            </th>
          </tr>
        </thead>
        <tbody v-if="tableData.length">
          <tr v-for="(d, did) in tableData" :key="`${prefix}-table-td-${did}`">
            <td v-if="options.checkAll" :width="options.checkBoxHasUser ? 110 : 60">
              <div class="mr-0"
              :class="multiple ? 'checkbox checkbox-inline' : 'radio radio-inline'">
                <input
                :type="multiple ? 'checkbox' : 'radio'"
                :name="`${prefix}-td-item${multiple? '[]' : ''}`"
                :id="`${prefix}-check-${did}`"
                :value="d.id"
                :disabled="prefix=='cancel-dail' && d.progress==-1 || d.progress >= 1"
                v-model="selected"
                @change="$emit('update', selected)">
                <label :for="`${prefix}-check-${did}`"></label>
              </div>
              <span v-if="options.checkBoxHasUser">
                <span class="no">{{d.index}}</span>
                <span class="info text-center">
                    <level :lv="Number(d.vip_level)" />
                    <avatar :gender="Number(d.sex)" :size="20" />
                </span>
              </span>
            </td>
            <td
            v-for="(col, cid) in options.columns"
            :width="col.width || '*'"
            :key="`table-td-col-${cid}`"
            :class="{
              'text-right': col.align && col.align=='right'
            }">
                <span v-if="col.template" v-html="col.template(d)"></span>
                <span v-else-if="col.name==='name' && prefix=='result-table'">
                    <span class="d-inline-block text-center align-middle mr-1">
                        <level :lv="Number(d.vip_level)" /><br/>
                        <avatar :gender="Number(d.sex)" :size="20" />
                    </span>
                    <span class="name align-middle">
                        {{ d[col.name] }}
                    </span>
                </span>
                <a v-else-if="col.click" @click="col.click(d)" class="link text-underline">{{d[col.name]}}</a>
                <span v-else>{{d[col.name]}}</span>
            </td>
          </tr>
        </tbody>
        <tbody v-else>
          <tr>
            <td :colspan="options.columns.length+1" class="empty-block bg-white">
              <h3 class="bg-white">{{remoteDataMsg}}</h3>
            </td>
          </tr>
        </tbody>
      </table>
    </section>
</template>

<script>
import Level from '../Level'
import Avatar from '../Avatar'
import { commonMixin } from '../../mixins/common'
import { setTimeout } from 'timers';

export default {
    name: "DataTable",
    mixins: [commonMixin],
    components: {Level, Avatar},
    props: {
        multiple: {
            type: Boolean,
            default: true
        },
        prefix:{
            type: String,
            default: ''
        },
        selection: {
            type: Array,
            default: null
        },
        remoteDataMsg:{
            type: String,
            default: ''
        },
        currentPage:{
            type: Number,
            default: 0
        },
        totalpages:{
            type: Number,
            default: 0
        },
        scrollDataLoading:{
            type: Boolean,
            default: false
        },
        tableData: {
            type: Array,
            default: () => {
                return []
            }
        },
        options: {
            type: Object,
            default: () => {
                return null
            }
        }
    },
    data() {
        return {
            tableList: this.tableData,
            order: {
                index: 0,
                asc: true
            },
            checkAll: 0,
            selected: [],
            currentWidth: 0
        }
    },
    watch: {
      "checkAll": "doCheckAll",
      "selection": 'selectionChange'
    },
    methods: {
      selectionChange (val) {
        this.selected = val
      },
      doCheckAll(val) {
        if(val) {
          this.tableData.forEach(data => {
            if(data.id !== '') 
              this.selected.push(data.id)
          });
        } else {
          this.selected = []
        }

        this.$emit('update', this.selected)
      },
      columnSorting (i) {
            if(!this.options.sorting) return;
            if(this.order.index===i) {
                this.order.asc = !this.order.asc
            }
            else {
                this.order.index = i
                this.order.asc = true
            }
            this.options.sorting(i + 1)
        }
    },
    mounted () {
        this.currentWidth = this.$el.clientWidth * (this.$el.clientWidth < 1250 ? 0.95 : 1)
    }
}
</script>

<style lang="scss" scoped>
.newTable thead tr,
.newTable tbody tr {
  
  td {
    word-break: break-all;
  }
  
  &.sorting,
  &.sorting_desc {
    position: relative;
    padding-right: 25px !important;
    
    &:before,
    &:after {
      content: '';
      position: absolute;
      width: 0px;
      height: 0px;
      display: block;
      top: 0;
      bottom: 0;
      right: 6px;
      margin: auto;
    }

    &:after {
      top: 18px;
      border-top: 6px solid transparentize(#027A39, .5);
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-bottom: 6px solid transparent;
    }

    &:before {
      top: -13px;
      border-bottom: 6px solid transparentize(#027A39, .5);
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-top: 6px solid transparent;
    }
  }

  &.sorting_asc:before {
    border-bottom: 6px solid transparentize(white, .5);
  }

  &.sorting_desc:after {
    border-top: 6px solid transparentize(white, .5);
  }

  &.no-sort:before,
  &.no-sort:after {
    display: none;
  }
}

</style>
