//pdf
export const getPostalPDF = async (userId, csidAry) => {
  if(AJADev) {
    return await new Promise((resol, reject) => {
      axios({
        methods: "POST",
        url:'/assets/test.pdf',
        data: reqBodyMaker.printPDF(userId, csidAry, 'postPDF'),
        responseType: 'arraybuffer'
      }).then(response => {
        // console.log(`Got[${debugMsg}]Res:${apiUrl}`)
        console.log(response)
        if (response.status === 200) {
          resol(response.data);
        } else {
          console.error(response);
          reject(response.status);
        }
      })
      .catch(error => {
        console.error(error);
        reject(error);
      })
    })
  } else {
    let newArr = [];
    for(let i=0; i<csidAry.length; i++){
      newArr.push(csidAry[i]);
    }

    // console.log(newArr);
    
    return await axiosWrap('Post', `/RestApiAdapter/MXCL_2002/postPDF`, reqBodyMaker.printPDF(userId, csidAry, 'postPDF'), 'arraybuffer', 'pdf')
  }
}

