const axios = require('axios')
import getCookie from '@/service/getCookie'

const axiosWrap = async (method, url, data = null, resType = null, debugMsg = '') => {
  let option = {
    'Content-Type': 'text/plain',
    'x-api-key': getCookie('api-keys') ? JSON.parse(getCookie('api-keys'))[0] : '',
    'x-api-host': 'mycrm',
  }
  
  let API_BASEURL = 'http://localhost:8081/MXWeb'
  if(window.location.href.indexOf('localhost') !== -1){
    API_BASEURL = 'http://localhost:8081/MXWeb'
  }else if(window.location.href.indexOf('tmycrm') !== -1){
    API_BASEURL = 'https://tcrmapi.cathaylife.com.tw/MXWeb'
  }else if(window.location.href.indexOf('smycrm') !== -1){
    API_BASEURL = 'https://scrmapi.cathaylife.com.tw/MXWeb'
  }else{
    API_BASEURL = `http://${window.location.href.match(/\d*\.\d*\.\d*\.\d*/g)}:8081/MXWeb`
  }
  const apiUrl = `${API_BASEURL}${url}`
  
  console.log(`Sent[${debugMsg}]Req:${method},url:${apiUrl}`)
  console.log(data)
  
  
  return new Promise((resolve, reject) => {
    axios({
      method: method,
      // url: `${API_PROXY}${API_BASEURL}${url}`,
      url: apiUrl,
      params: method === 'get' ? data : null,
      data: method === 'get' ? null : data,
      headers: option,
      responseType: resType,
      timeout: 60000,
      withCredentials: true,
    })
    .then(response => {
      console.log(`Got[${debugMsg}]Res:${apiUrl}`)
      console.log(response)
      if (response.status === 200) {
        resolve(response.data);
      } else {
        console.error(response);
        let errorData = {
          header:{
            message: " ",
            status: "400",
            userDeviceIp: "",
            userId: "",
          },
          body:{},
        }
        resolve(errorData);
      }
    })
    .catch(error => {
      console.error(error);
      let errorData = {
        header:{
          message: " ",
          status: "400",
          userDeviceIp: "",
          userId: "",
        },
        body:{},
      }
      resolve(errorData);
      // reject(error);
    })
  })
}

export default axiosWrap;